export default class Api {
    constructor(config) {
        this._url = config.url;
        this.headers = config.headers;
    }

    getAllCards() {
        fetch(this._url, {
            method: "GET", 
            headers: this.headers,
        })
        .then(res => res.json())
        .then((result) => {
          console.log(result);
        })
    }
}